'use strict';

//import GGBView from "./views/GeogebraView.js";
import TreeView from "./views/TreeView.js";
import {requestData} from "./utilities.js";

const categories = document.querySelectorAll(".sidebar__list");
const app_container = document.querySelector('.app-container');
const ggb_element = document.querySelector('#ggb-element');
const welcome = document.querySelector('.welcome');

window.addEventListener('load', ()=>{
	let resizeObserver = new ResizeObserver(() => {
		let WIDTH = app_container.getBoundingClientRect().width;
		let HEIGHT = app_container.getBoundingClientRect().height;
		params = {
			"id":"ggbApplet",
            "appName":"graphing",
           	"width": WIDTH,
           	"height": (HEIGHT - 45),
            "showToolBar":true,
            "showMenuBar":true,
            "showAlgebraInput":true,
            "errorDialogsActive":true,
		}
		let applet = new GGBApplet(params, true);
		applet.inject(ggb_element);
		//GGBView.renderGGB(ggb_element);
	});
	resizeObserver.observe(app_container);
});


TreeView.init();

categories.forEach(category => {
	category.addEventListener('click', (e)=>{
		let element = e.target;
		if(element.classList.contains('select')){
			welcome.classList.add('hidden');
			
			requestData(element);		
		}
	});
});








